import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Carousel from 'nuka-carousel';
import { ChevronLeft } from 'styled-icons/feather/ChevronLeft';
import { ChevronRight } from 'styled-icons/feather/ChevronRight';

import styled from 'styled-components';

const Title = styled.div`
  font-size: 1rem;
  font-weight: 500;
`;
const Subtitle = styled.div`
  font-size: 0.8rem;
`;

const LeftButtonIcon = styled(ChevronLeft)`
  width: 2.5rem;
  color: #f09819;
  display: ${props => (props.enabled ? 'block' : 'none')};
`;

const RightButtonIcon = styled(ChevronRight)`
  width: 2.5rem;
  color: #f09819;
  display: ${props => (props.enabled ? 'block' : 'none')};
`;

const styles = {
  card: {
    maxWidth: '100%',
  },
  media: {
    // ⚠️ object-fit is not supported by IE 11.
    // objectFit: 'cover',
    objectPosition: '50% 50%',
  },
};

function ProjectShowCard(props) {
  const { classes, images, title } = props;
  return (
    <Card className={classes.card}>
      <CardActionArea>
        <Carousel
          renderCenterLeftControls={({ previousSlide, currentSlide }) => (
            <LeftButtonIcon onClick={previousSlide} enabled={currentSlide !== 0}>
              Previous
            </LeftButtonIcon>
          )}
          renderCenterRightControls={({ nextSlide, currentSlide }) => (
            <RightButtonIcon onClick={nextSlide} enabled={currentSlide !== images.length - 1}>
              Next
            </RightButtonIcon>
          )}
        >
          {images.map(item => (
            <CardMedia component="img" className={classes.media} height="350" image={item} />
          ))}
        </Carousel>

        <CardContent>
          <Title>{title}</Title>
          <Subtitle>A personal website</Subtitle>
        </CardContent>
      </CardActionArea>
    </Card>
  );
}

ProjectShowCard.propTypes = {
  classes: PropTypes.object.isRequired,
  title: PropTypes.string.isRequired,
  image: PropTypes.array.isRequired,
};

export default withStyles(styles)(ProjectShowCard);
